import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import './Register.css';
import { FaRocket, FaEye, FaEyeSlash, FaCheckCircle, FaTimesCircle, FaGoogle, FaLinkedin } from 'react-icons/fa';
import ParticlesBg from 'particles-bg';

const Register = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
    agreeToTerms: false
  });
  const [errors, setErrors] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [passwordStrength, setPasswordStrength] = useState(0);
  const [isEmailValid, setIsEmailValid] = useState(null);
  const [isPhoneValid, setIsPhoneValid] = useState(null);
  const navigate = useNavigate();

  const validateEmail = (email) => {
    const emailRegex = /\S+@\S+\.\S+/;
    setIsEmailValid(emailRegex.test(email));
  };

  const validatePhone = (phone) => {
    const phoneRegex = /^\+?[\d\s-]{10,}$/;
    setIsPhoneValid(phoneRegex.test(phone));
  };

  const calculatePasswordStrength = (password) => {
    let strength = 0;
    if (password.length >= 8) strength += 1;
    if (/[A-Z]/.test(password)) strength += 1;
    if (/[0-9]/.test(password)) strength += 1;
    if (/[^A-Za-z0-9]/.test(password)) strength += 1;
    setPasswordStrength(strength);
  };

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    const newValue = type === 'checkbox' ? checked : value;
    
    setFormData(prev => ({
      ...prev,
      [name]: newValue
    }));

    if (name === 'email') validateEmail(value);
    if (name === 'phone') validatePhone(value);
    if (name === 'password') calculatePasswordStrength(value);
  };

  const validateForm = () => {
    let newErrors = {};
    if (!formData.name) newErrors.name = 'Name is required';
    if (!formData.email) newErrors.email = 'Email is required';
    else if (!/\S+@\S+\.\S+/.test(formData.email)) newErrors.email = 'Email address is invalid';
    if (!formData.password) newErrors.password = 'Password is required';
    else if (formData.password.length < 8) newErrors.password = 'Password must be at least 8 characters';
    if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = 'Passwords do not match';
    }
    if (formData.phone && !/^\+?[\d\s-]{10,}$/.test(formData.phone)) {
      newErrors.phone = 'Invalid phone number';
    }
    if (!formData.agreeToTerms) {
      newErrors.agreeToTerms = 'You must agree to the terms and conditions';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (validateForm()) {
      setIsLoading(true);
      try {
        const registerResponse = await fetch('http://127.0.0.1:5002/api/auth/register', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(formData),
        });
        const registerData = await registerResponse.json();

        if (registerResponse.ok) {
          const loginResponse = await fetch('http://127.0.0.1:5002/api/auth/login', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ email: formData.email, password: formData.password }),
          });
          const loginData = await loginResponse.json();

          if (loginResponse.ok) {
            localStorage.setItem('access_token', loginData.access_token);
            setIsSuccess(true);
            setErrorMessage('');

            setTimeout(() => {
              navigate('/profile');
            }, 3000);
          } else {
            setErrorMessage(loginData.error || 'Login failed.');
          }
        } else {
          setErrorMessage(registerData.error || 'Registration failed.');
        }
      } catch (error) {
        setErrorMessage('Network error. Please try again later.');
      } finally {
        setIsLoading(false);
      }
    }
  };

  return (
    <div className="register-container">
      <ParticlesBg type="cobweb" bg={true} color="#4fc3f7" />
      
      <motion.div 
        className="holographic-panel"
        initial={{ opacity: 0, y: -50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 1, ease: "easeOut" }}
      >
        <motion.img 
          src="/eduverse-logo.png"
          alt="EduVerse"
          className="register-logo"
          whileHover={{ rotate: 360, scale: 1.1 }}
          transition={{ duration: 0.8 }}
        />

        <motion.h1 
          className="register-title"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.5 }}
        >
          Join the Future of Learning and Collaboration
        </motion.h1>

        <AnimatePresence>
          {isSuccess ? (
            <motion.div 
              className="success-container"
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              exit={{ scale: 0 }}
            >
              <FaRocket className="rocket-icon" />
              <p className="success-text">Welcome aboard! Preparing your EduVerse journey...</p>
            </motion.div>
          ) : (
            <motion.form 
              onSubmit={handleSubmit}
              className="register-form"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
            >
              <div className="social-signup">
                <motion.button
                  type="button"
                  className="social-button google"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  <FaGoogle /> Sign up with Google
                </motion.button>
                <motion.button
                  type="button"
                  className="social-button linkedin"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  <FaLinkedin /> Sign up with LinkedIn
                </motion.button>
              </div>

              <div className="divider">
                <span>or register with email</span>
              </div>

              <div className="input-group">
                <motion.input
                  type="text"
                  name="name"
                  placeholder="Full Name"
                  value={formData.name}
                  onChange={handleChange}
                  whileFocus={{ scale: 1.02 }}
                  className="neon-input"
                />
              </div>

              <div className="input-group">
                <motion.input
                  type="email"
                  name="email"
                  placeholder="Email"
                  value={formData.email}
                  onChange={handleChange}
                  whileFocus={{ scale: 1.02 }}
                  className={`neon-input ${isEmailValid === true ? 'valid' : isEmailValid === false ? 'invalid' : ''}`}
                />
                {isEmailValid !== null && (
                  <motion.div 
                    initial={{ scale: 0 }}
                    animate={{ scale: 1 }}
                    className="validation-icon"
                  >
                    {isEmailValid ? <FaCheckCircle className="valid-icon" /> : <FaTimesCircle className="invalid-icon" />}
                  </motion.div>
                )}
              </div>

              <div className="input-group">
                <motion.input
                  type={showPassword ? "text" : "password"}
                  name="password"
                  placeholder="Password"
                  value={formData.password}
                  onChange={handleChange}
                  whileFocus={{ scale: 1.02 }}
                  className="neon-input"
                />
                <motion.div 
                  className="password-toggle"
                  whileHover={{ scale: 1.1 }}
                  onClick={() => setShowPassword(!showPassword)}
                >
                  {showPassword ? <FaEyeSlash /> : <FaEye />}
                </motion.div>
                <div className="strength-meter">
                  {[...Array(4)].map((_, i) => (
                    <motion.div
                      key={i}
                      className={`strength-segment ${i < passwordStrength ? 'active' : ''}`}
                      initial={{ width: 0 }}
                      animate={{ width: '100%' }}
                    />
                  ))}
                </div>
              </div>

              <div className="input-group">
                <motion.input
                  type={showConfirmPassword ? "text" : "password"}
                  name="confirmPassword"
                  placeholder="Confirm Password"
                  value={formData.confirmPassword}
                  onChange={handleChange}
                  whileFocus={{ scale: 1.02 }}
                  className="neon-input"
                />
                <motion.div 
                  className="password-toggle"
                  whileHover={{ scale: 1.1 }}
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                >
                  {showConfirmPassword ? <FaEyeSlash /> : <FaEye />}
                </motion.div>
              </div>

              <div className="input-group">
                <motion.input
                  type="tel"
                  name="phone"
                  placeholder="Phone Number (Optional)"
                  value={formData.phone}
                  onChange={handleChange}
                  whileFocus={{ scale: 1.02 }}
                  className={`neon-input ${isPhoneValid === true ? 'valid' : isPhoneValid === false ? 'invalid' : ''}`}
                />
              </div>

              <div className="terms-group">
                <label className="terms-label">
                  <input
                    type="checkbox"
                    name="agreeToTerms"
                    checked={formData.agreeToTerms}
                    onChange={handleChange}
                  />
                  <span className="checkmark"></span>
                  I agree to the Terms and Conditions
                </label>
              </div>

              <motion.button
                type="submit"
                className="submit-button"
                disabled={isLoading}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                {isLoading ? (
                  <motion.div 
                    className="loading-spinner"
                    animate={{ rotate: 360 }}
                    transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                  />
                ) : 'Join EduVerse'}
              </motion.button>
            </motion.form>
          )}
        </AnimatePresence>

        {errorMessage && (
          <motion.div 
            className="error-message"
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: 20 }}
          >
            {errorMessage}
          </motion.div>
        )}

        <motion.button
          onClick={() => navigate('/login')}
          className="login-link"
          whileHover={{ scale: 1.05, textShadow: "0 0 8px rgb(255,255,255)" }}
        >
          Already an explorer? Launch your journey
        </motion.button>
      </motion.div>
    </div>
  );
};

export default Register;
